import React from 'react';
import './Navigation.css';
import navigationLogo from '../../images/nav-logo.svg';



const Navigation = () => {

    return(
        <nav class="fixednav">
                <img src={navigationLogo} alt="Company logo" />
                <div class="links">
                    <a href="#">Product</a>
                    <a href="#">About Us</a>
                    <a href="#">Contact</a>
                </div>
        </nav>
    )
};


export default Navigation;