import React from 'react';
import heroImg from '../../images/hero-img.svg'
import './Landing.css';


const Landing = () => {
    return(
        <div className="landing-container">
            <div className="left-container">
                <img src={heroImg} alt="SVG of a happy lady" />
            </div>
            <div className="right-container">
                <h1>We make it easier for people to  
                    <span className="red-header-text"><br/>achieve their dreams</span>
                </h1>
            <p className="first-p">
                Great Things are created together!<br/>
                <span className="red-header-text">Our platform connects those who want to make a difference together</span>
            </p>
            <button className="landing-btn">Sounds good?</button>
            </div>
        </div>
    )
};


export default Landing;